import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { deleteTask, editTask, toggleTask } from '../store/TasksSlice';
import { usePrevious } from '../utils/Utils';

function Task({ id, name, isCompleted }) {
  const dispatch = useDispatch();

  const [isEditing, setIsEditing] = useState(false);
  const [newName, setNewName] = useState('');

  const editFieldRef = useRef(null);
  const editButtonRef = useRef(null);

  const wasEditing = usePrevious(isEditing);

  useEffect(() => {
    if (!wasEditing && isEditing) {
      editFieldRef.current.focus();
    }
    if (wasEditing && !isEditing) {
      editButtonRef.current.focus();
    }
  }, [wasEditing, isEditing]);

  function handleSubmit(e) {
    e.preventDefault();
    setIsEditing(false);
    if (!newName.trim()) {
      return;
    }
    dispatch(editTask({ id, newName }));
    setNewName('');
  }

  const editingTemplate = (
    <form className="todo-container" onSubmit={handleSubmit}>
      <input id={id} type="checkbox" className="chk hidden" disabled />
      <input
        id={id}
        className="todo-task-name"
        type="text"
        value={newName || name}
        onChange={e => setNewName(e.target.value)}
        onBlur={() => setIsEditing(false)}
        ref={editFieldRef}
        maxLength="75"
      />
      <div className="todo-actions hidden">
        <button type="submit" className="btn save-btn">
          Save
        </button>
        <button
          type="button"
          className="btn cancel-btn"
          onClick={() => setIsEditing(false)}
        >
          Cancel
        </button>
      </div>
    </form>
  );

  const viewTemplate = (
    <div className="todo-container">
      <input
        id={id}
        type="checkbox"
        className="chk"
        defaultChecked={isCompleted}
        onChange={() => dispatch(toggleTask(id))}
      />
      <label className={`lbl${isCompleted ? ' crossout' : ''}`} htmlFor={id}>
        {name}
      </label>
      <div className="todo-actions">
        <button
          type="button"
          className="btn edit-btn"
          onClick={() => setIsEditing(true)}
          ref={editButtonRef}
        >
          Edit
        </button>
        <button
          type="button"
          className="btn delete-btn"
          onClick={() => dispatch(deleteTask(id))}
        >
          Delete
        </button>
      </div>
    </div>
  );

  return <li>{isEditing ? editingTemplate : viewTemplate}</li>;
}
Task.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  isCompleted: PropTypes.bool.isRequired,
};
export default Task;
