/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { useDispatch, useSelector } from 'react-redux';
import ClearButton from '../components/ClearButton';
import Form from '../components/Form';
import FilterButtonList from '../components/FilterButtonList';
import DraggableTask from '../components/DraggableTask';
import {
  reorderTasks,
  clearCompletedTasks,
  selectTasksByFilter,
  selectActiveTasksLength,
} from '../store/TasksSlice';

const Todo = () => {
  const dispatch = useDispatch();

  const remainingTasks = useSelector(selectActiveTasksLength);
  const remainingTasksText = `${remainingTasks} ${
    remainingTasks !== 1 ? 'tasks' : 'task'
  } left`;

  const onClearCompleted = () => dispatch(clearCompletedTasks());
  const onDragEndHandler = ({ source, destination }) =>
    dispatch(reorderTasks({ source, destination }));

  const taskList = useSelector(selectTasksByFilter).map((task, index) => (
    <DraggableTask
      id={task.id}
      name={task.name}
      isCompleted={task.isCompleted}
      index={index}
      key={task.id}
    />
  ));

  return (
    <div className="app-container">
      <h2 className="header">Todo</h2>
      <Form />
      <div className="todo-list-container">
        <DragDropContext onDragEnd={onDragEndHandler}>
          <Droppable droppableId="todo-list-droppable-area" type="group">
            {provided => (
              <ul
                {...provided.droppableProps}
                ref={provided.innerRef}
                className="todo-list"
              >
                {taskList}
                {provided.placeholder}
              </ul>
            )}
          </Droppable>
        </DragDropContext>
        <div className="todo-list-footer">
          <h2 className="tasks-counter" tabIndex="-1">
            {remainingTasksText}
          </h2>
          <FilterButtonList />
          <ClearButton clearCompleted={onClearCompleted} />
        </div>
      </div>
      <h3 className="footer">Drag and drop to reorder list</h3>
    </div>
  );
};
export default Todo;
