import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { nanoid } from 'nanoid';
import { addTask } from '../store/TasksSlice';

function Form() {
  const [name, setName] = useState('');
  const dispatch = useDispatch();

  function handleSubmit(e) {
    e.preventDefault();

    if (!name.trim()) {
      return;
    }

    dispatch(
      addTask({ id: `todo-${nanoid()}`, name: name, isCompleted: false })
    );

    setName('');
  }

  function handleChange(e) {
    setName(e.target.value);
  }

  return (
    <form className="new-task-form todo-container" onSubmit={handleSubmit}>
      <input type="checkbox" className="chk hidden" disabled />
      <input
        type="text"
        id="new-todo-input"
        className="todo-task-name"
        name="text"
        autoComplete="off"
        value={name}
        onChange={handleChange}
        placeholder="Type new task here"
        maxLength="75"
      />
      <button type="submit" className="btn add-btn hidden">
        Add
      </button>
    </form>
  );
}
export default Form;
